import React, { Component } from "react";
import "./styles.css";

export default class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      Subscribed: localStorage.getItem("Subscribed")
    };
  }

  render() {
    const changeState = () => {
      if (this.state.Subscribed === false) {
        this.setState({
          Subscribed: true
        });
      } else {
        this.setState({
          Subscribed: false
        });
      }
      localStorage.setItem("Subscribed", this.state.Subscribed);
      console.log(this.state.Subscribed);
    };
    return (
      <>
        <button type="button" onClick={changeState}>
          {this.state.Subscribed === true ? "Unsubscribe" : "Subscribe"}
        </button>
      </>
    );
  }
}
